<template>
  <b-card id="impCard" class="borderAlign" no-body>
    <b-tabs v-model="tabIndex">
      <b-tab
        :title-link-class="linkClass(0)"
        :title="$t('EXPENSEITEMCOMPARISON')"
      >
        <b-row class="d-flex justify-content-center">
          <b-modal
            id="modal-tall"
            centered
            variant="secondary"
            modal-class="py-0"
            body-class="py-0"
            size="md"
            footer-class="border-0"
            header-class="border-0 bg-white"
          >
            <template v-slot:modal-header="{ cancel }">
              <div class="w-100 d-flex justify-content-end">
                <button
                  @click="cancel()"
                  class="btn-sm border-dark text-dark font-weight-bold text-sm m-0 bg-white"
                >
                  X
                </button>
              </div>
            </template>
            <div class="my-0 py-0 px-2">
              <ul class="list-unstyled my-0 py-0">
                <li
                  v-for="(OVERALL_JUDGEMENT, index) in $t(
                    'DASHBOARD.OVERALL_JUDGEMENTS'
                  )"
                  :key="index"
                >
                  <span class="font-weight-bold block">
                    {{ OVERALL_JUDGEMENT.LABEL }}
                  </span>
                  <p class="mb-0">{{ OVERALL_JUDGEMENT.VALUE }}</p>
                </li>
              </ul>
            </div>
            <template v-slot:modal-footer="{ cancel }">
              <div class="w-100">
                <button
                  @click="cancel()"
                  class="btn-sm float-left btn-custom-color px-1"
                >
                  {{ $t("CLOSE") }}
                </button>
              </div>
            </template>
          </b-modal>
        </b-row>
        <b-row>
          <b-col cols="12" class="d-flex sm-buttons-center">
            <div>
              <b-button
                @click="getThreeMonthsDetails"
                :class="{ mntbtn: mntBtnFlag, mnt2btn: !mntBtnFlag }"
                class="mntborder"
                >3ケ月</b-button
              >
              <b-button
                @click="getSixMonthsDetails"
                :class="{ mntbtn: mntBtnFlag2, mnt2btn: !mntBtnFlag2 }"
                class="mnt2border"
                >6ケ月</b-button
              >
              <b-button
                @click="getAllMonthsDetails"
                :class="{ mntbtn: mntBtnFlag3, mnt2btn: !mntBtnFlag3 }"
                class="border-radius-imprv"
                >12ケ月</b-button
              >
            </div>
          </b-col>
        </b-row>
        <br />

        <div>
          <b-row class="w-full mb-2 no-gutters">
            <b-col v-if="tabLoading" class="text-center">
              <i class="fa fa-spinner fa-spin fa-2x" />
            </b-col>
            <b-col v-else cols="12" xl="10" lg="9">
              <ImprovementExpenseComp
                :dataset="dataset"
                :colors="colors"
                :labels="labels"
                :total-months="selectedOption"
                :start-year="startYear"
                :start-month="startMonth"
                :x-labels-prop="xLabels"
                :x-min-max-prop="xLablesMinMax"
                :key="redraw"
                expense-type="expense"
              />
            </b-col>

            <b-col id="expenceCheckBox" cols="12" xl="2" lg="2" class="d-none d-lg-block">
              <span  class="d-flex flex-wrap expense-btn">
                <b-form-checkbox-group
                  id="checkbox-group-expense"
                  @change="drawGraph"
                  v-model="selected_expense"
                  :options="expenseOptions"
                  class="small-fs"
                  size="sm"
                  stacked
                ></b-form-checkbox-group>
              </span>
            </b-col>
          </b-row>
        </div>

        <b-row class="d-flex justify-content-between mt-4 mb-3">
          <b-col cols="12" class="d-flex justify-content-center">
            <div
              class="py-2 px-2 pillButtons bg-white d-flex justify-content-center align-items-center"
            >
              <b-link
                @click="goPrevMonth"
                href="javascript:;"
                class="link d-flex justify-content-center align-items-center"
              >
                <span class="">
                  <i
                    class="fa fa-caret-left fa-2x text-muted mr-2"
                    aria-hidden="true"
                  ></i>
                </span>
                <span class="mr-2 dark-blue">{{ $t("TOPREVIOUSMONTH") }}</span>
              </b-link>
              &nbsp;|
              <b-link
                @click="goNextMonth"
                href="javascript:;"
                class="link ml-2 d-flex justify-content-center align-items-center"
              >
                <span class="dark-blue">{{ $t("TONEXTMONTH") }}</span>
                <span class="">
                  <i
                    class="fa fa-caret-right fa-2x text-muted ml-2"
                    aria-hidden="true"
                  ></i>
                </span>
              </b-link>
            </div>
          </b-col>
        </b-row>
        <b-row class="d-md-block d-lg-none">
          <b-col cols="12">
            <button
              @click.prevent="toggleSubtree = !toggleSubtree"
              class="w-100 px-3 py-2 dropdownToggle border-0 d-flex justify-content-between align-items-center"
            >
              <span>費目選択</span>
              <span
                ><i
                  :class="
                    toggleSubtree ? 'fa fa-caret-down' : 'fa fa-caret-down'
                  "
                ></i
              ></span>
            </button>
            <b-row v-if="toggleSubtree" class="my-2 dropdownToggleBody mx-auto">
              <b-col cols="12" class="checkboxExpenseItems submenuScroll">
                <!-- <b-col class="submenuScroll"> -->
                <SubMenuTree
                  :tree-options="treeOptions"
                  @update-multiselect-exp-item="getExpItmCodeArray"
                  :treeTitle="treeTitle"
                  class="mt-1"
                />
                <!-- </b-col> -->
                <div class="w-100 d-flex mb-2 px-2">
                  <span>
                    <b-button
                      @click.prevent="applyCheckExpItem"
                      variant="primary"
                      class="py-1 mblBtn ml-1"
                      >{{ $t("UPDATE") }}</b-button
                    >
                  </span>
                  <span>
                    <b-button
                      @click.prevent="clearSubmenu()"
                      class="clearButton ml-3 mblBtn py-1"
                      >{{ $t("CLEAR") }}</b-button
                    >
                  </span>
                </div>
              </b-col>
            </b-row>
            <b-row>
              <b-col
                cols="12"
                class="w-100 mt-3 justify-content-center text-center"
              >
                <b-button class="expandButton">
                  <img
                    src="@/assets/hatena-icon.svg"
                    class="navbar-brand-full p-1"
                    width="10%"
                    alt="Expense Reduction Management Logo"
                  />
                  コストについての説明
                  <span @click.prevent="showFirst = !showFirst">
                    <span v-if="showFirst">
                      <i
                        class="fa fa-minus mt-1 text-center float-right journal-minimizer"
                        aria-hidden="true"
                      ></i>
                    </span>
                    <span v-if="!showFirst">
                      <i
                        class="fa fa-plus mt-1 text-center ipadBtn float-right journal-minimizer"
                        aria-hidden="true"
                      ></i>
                    </span>
                  </span>
                </b-button>
              </b-col>
              <b-col v-if="showFirst" cols="12" sm="3" md="12">
                <ul class="list-unstyled mt-3 list-legend">
                  <li
                    v-for="(legend, index) in $t('GRAPH.LEGENDS')"
                    :key="index"
                    class="d-flex justify-content-start mb-3"
                  >
                    <div>
                      <span
                        :style="`color:${legend.COLOR}`"
                        class="block py-1 mr-1 font-weight-bold"
                      >
                        {{ legend.LEGEND }}
                      </span>
                      <br />
                      <span class="block py-1">{{ legend.MEANING }}</span>
                    </div>
                  </li>
                </ul>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </b-tab>
      <b-tab
        :title-link-class="linkClass(1)"
        :title="$t('JUDGEMENTCOMPARISON')"
      >
        <ImprovementExpenseMonth></ImprovementExpenseMonth>
      </b-tab>
    </b-tabs>
  </b-card>
</template>

<script>
import ImprovementExpenseComp from "@/components/graphs/ImprovementExpenseCompNew.vue";
import SubMenuTree from "@/components/SubMenuTreeCheckbox.vue";
import api from "@/shared/api";
import { mapGetters } from "vuex";
import { endPoint } from "@/shared/endPoint";
import ImprovementExpenseMonth from "@/views/business/dashboard/ImprovementExpenseMonth";

export default {
  components: { ImprovementExpenseComp, SubMenuTree, ImprovementExpenseMonth },
  data() {
    return {
      showFirst: false,
      toggleSubtree: false,
      selected_expense: [],
      expenseOptions: [],
      reDrawGraph: 0,
      tabIndex: 0,
      mntBtnFlag: false,
      mntBtnFlag2: true,
      mntBtnFlag3: false,
      tabLoading: false,
      treeTitle: " ",
      checkedExpenseItemCodeArray: [],
      selectedOption: 6,
      expenseType: "expense",
      selectExpenseItem: this.$t("EXPENSEITEMSELECTION"),
      redraw: 0,
      dataset: [],
      startMonth: "",
      startYear: "",
      xLabels: [],
      xLablesMinMax: [],
      treeOptions: {
        checkbox: true,
      },
      optionList: [
        { key: "12か月", value: "12" },
        { key: "6か月", value: "6" },
        { key: "3か月", value: "3" },
      ],
      colors: ["#04aeec", "#64e6a4", "#f5f886", "#ff9393", "#bfbfbf"],
      colors2: [
        "#ffd966",
        "#00b0f0",
        "#a9d08e",
        "#c65911",
        "#ff5353",
        "#a6a6a6",
      ],
      labels: [
        this.$t("OVERALLJUDGEMENTCHART.SLIM"),
        this.$t("OVERALLJUDGEMENTCHART.HEALTHY"),
        this.$t("OVERALLJUDGEMENTCHART.FAT"),
        this.$t("OVERALLJUDGEMENTCHART.METABO"),
        this.$t("OVERALLJUDGEMENTCHART.SPOILER"),
      ],
      labels2: [
        this.$t("RANKEXCELLENCE.EXCELLENCE"),
        this.$t("RANKEXCELLENCE.GREATCOMPANY"),
        this.$t("RANKEXCELLENCE.GOODCOMPANY"),
        this.$t("RANKEXCELLENCE.STANDARD"),
        this.$t("RANKEXCELLENCE.OVERWEIGHT"),
        this.$t("RANKEXCELLENCE.METABOLIC"),
      ],
    };
  },
  computed: {
    ...mapGetters({
      costInfoTree: "getCostInfoTreeList",
      checkExpItemList: "getCheckedExpItemCodeArray",
      userAccountData: "getUserDetails",
    }),
  },
  async mounted() {
    // console.log("costInfoTree", this.costInfoTree);
    await this.getStoreData();
    this.getImprvExpData();
    this.constructTreeArray();
    this.displayButtons();
  },
  methods: {
    getStoreData(){
    //   this.checkedExpenseItemCodeArray = await this.costInfoTree.map(
    //   (Item) => Item.expense_item_cd
    // );
    this.costInfoTree.forEach(item => {
          let expenseData = {
            expense_item_cd : item.expense_item_cd,
            accounts_name_cd : item.accounts_name_cd
          }
          this.checkedExpenseItemCodeArray.push(expenseData);
        });
    this.$store.dispatch(
      "updateCheckedExpItemCodeArray",
      this.checkedExpenseItemCodeArray
    );
    },
    toggle() {
      this.$refs["tree-modal"].hide();
    },

    linkClass(idx) {
      if (this.tabIndex === idx) {
        return ["bg-white", "text-Blue"];
      } else {
        return ["bg-grayCustom", "text-white", "dynamic-height"];
      }
    },
    getThreeMonthsDetails() {
      this.selectedOption = 3;
      (this.mntBtnFlag = true),
        (this.mntBtnFlag2 = false),
        (this.mntBtnFlag3 = false),
        this.getImprvExpData(this.selectedOption);
    },
    getSixMonthsDetails() {
      this.selectedOption = 6;
      (this.mntBtnFlag = false),
        (this.mntBtnFlag2 = true),
        (this.mntBtnFlag3 = false),
        this.getImprvExpData(this.selectedOption);
    },
    getAllMonthsDetails() {
      this.selectedOption = 12;
      (this.mntBtnFlag = false),
        (this.mntBtnFlag2 = false),
        (this.mntBtnFlag3 = true),
        this.getImprvExpData(this.selectedOption);
    },
    getExpItmCodeArray(item) {
      this.checkedExpenseItemCodeArray = item;
    },
    displayButtons() {
      this.costInfoTree.forEach((item) => {
        let iterateExpenseItem = {
          text: item.expense_item_nm ? item.expense_item_nm : "",
          value: {expense_item_cd : item.expense_item_cd,accounts_name_cd : item.accounts_name_cd}
        };
        if (item.expense_item_nm) {
          this.expenseOptions.push(iterateExpenseItem);
          this.selected_expense.push(iterateExpenseItem.value);
        }
        //this.selected_expense.push(item.expense_item_cd);
      });
    },
    drawGraph() {
      setTimeout(() => {
        this.getImprvExpData();
      }, 0);
    },
    async getImprvExpData(dropDown = false) {
      if (dropDown) {
        this.startMonth = "";
        this.startYear = "";
      }
      this.tabLoading = true;
      let { data } = await api.post(endPoint.EVALUATIONEXPENSEITEMCOMPAISON, {
        client_no: this.userAccountData.client_account_no,
        expense_item_cd: this.selected_expense,
        start_month: isNaN(parseInt(this.startMonth))
          ? ""
          : parseInt(this.startMonth),
        year: isNaN(parseInt(this.startYear)) ? "" : parseInt(this.startYear),
        total_month: this.selectedOption,
      });
      this.tabLoading = false;
      if (data.success) {
        this.dataset = data.data.clientJudgeInfoPerformanceListObject;
        this.startMonth = parseInt(data.data.start_month);
        this.startYear = parseInt(data.data.year);
        this.redraw = this.redraw + 1;
      } else {
        this.dataset = [
          {
            expenseItem: "",
            expense_item_cd: "",
            performance: [],
          },
        ];
        this.redraw = this.redraw + 1;
      }
    },

    showTreeModal() {
      this.$refs["tree-modal"].show();
    },
    onExpenseClick() {
      this.showTreeModal();
    },

    goPrevMonth() {
      if (parseInt(this.startMonth) == 1) {
        this.startYear = parseInt(this.startYear) - 1;
        this.startMonth = 12;
        this.getImprvExpData();
      } else {
        if (parseInt(this.startMonth) <= 12 && parseInt(this.startMonth) > 1) {
          this.startMonth = parseInt(this.startMonth) - 1;
          this.getImprvExpData();
        }
      }
    },
    goNextMonth() {
      if (parseInt(this.startMonth) >= 12) {
        this.startYear = parseInt(this.startYear) + 1;
        this.startMonth = 1;
        this.getImprvExpData();
      } else {
        if (parseInt(this.startMonth) >= 1 && parseInt(this.startMonth) < 12) {
          this.startMonth = parseInt(this.startMonth) + 1;
          this.getImprvExpData();
        }
      }
    },
    applyCheckExpItem() {
      this.$store.dispatch(
        "updateCheckedExpItemCodeArray",
        this.checkedExpenseItemCodeArray
      );
      this.selected_expense = this.checkedExpenseItemCodeArray;
      this.getImprvExpData();
      this.constructTreeArray();
      this.toggleSubtree = false;
    },

    clearSubmenu() {
      this.checkedExpenseItemCodeArray = [];
      this.$store.dispatch(
        "updateCheckedExpItemCodeArray",
        this.checkedExpenseItemCodeArray
      );
      this.selected_expense = this.checkedExpenseItemCodeArray;
      this.getImprvExpData();
      this.constructTreeArray();
      this.toggleSubtree = false;
    },
    constructTreeArray() {
      let mapData = [];
      console.log(this.costInfoTree, "this.costInfoTree,");
      let groupedData = this._.groupBy(this.costInfoTree, "accounts_name_nm");
      Object.keys(groupedData).forEach((key) => {
        let childrenArray = [];
        groupedData[key].forEach((item) => {
          let bindChildData;
          let findObject = this.checkExpItemList.find(element => element.expense_item_cd === item.expense_item_cd && element.accounts_name_cd === item.accounts_name_cd);
          // if (this.checkExpItemList.includes(item.expense_item_cd)) {
          if (findObject) {
            bindChildData = {
              text: item.expense_item_nm,
              state: { checked: true },
              expenseItemCode: item.expense_item_cd,
              accountNameCode: item.accounts_name_cd,
            };
          } else {
            bindChildData = {
              text: item.expense_item_nm,
              expenseItemCode: item.expense_item_cd,
              accountNameCode: item.accounts_name_cd,
            };
          }
          childrenArray.push(bindChildData);
        });
        let bindTreeData = {
          text: key,
          state: { expanded: true },
          children: childrenArray,
        };
        mapData.push(bindTreeData);
      });
      this.$store.dispatch("updateTree", mapData);
    },
  },
};
</script>
<style lang="scss">
.bg-grayCustom {
  background-color: #c4c4c4;
}
</style>
<style scoped lang="scss">
$cell_color: #ffffff;
$border_color: #221e16;
$time_font: 8px;
