  }
}
</style>
<style lang="scss">
$primary: #062854 !default;
$shadow-color: #dbd6d6;
$link-color: #6790b9;

@media only screen and (min-width: 991px) {
  .border-radius-imprv {
    border-top-left-radius: 0px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    border-bottom-left-radius: 0px;
  }

  .mnt2border {
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
  }
  .mntborder {
    border-top-left-radius: 4px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 4px;
  }
}

.dynamic-height {
  height: 29px;
  top: 4px;
  position: absolute;
  width: 7rem;
}
.small-width {
  min-width: 100px !important;
}
.normal-width {
  width: 80px !important;
}
#impCard .nav-tabs .nav-item {
  width: 7rem !important;
  margin-right: 8px;
  text-align: center;
  font-size: 11px;
}
#impCard .nav-tabs .nav-link.active {
  color: #033170;
  font-weight: 700;

  background: #fff !important;
}

.light-gray {
  background-color: #bbbbbb;
}
.tree-checkbox {
  border: 0px !important;
  border-radius: 50% !important;
  height: 15px !important;
  width: 15px !important;
  box-shadow: 0 0 10px $shadow-color;
  white-space: nowrap;
}
.tree-checkbox.checked:after {
  box-sizing: inherit !important;
  border: 1.5px solid #033170 !important;
  border-left: 0 !important;
  border-top: 0 !important;
  left: 6px !important;
  top: 2px !important;
  height: 6px !important;
  width: 0px !important;
  background: #033170 0% 0% no-repeat padding-box;
}
.checkboxExpenseItems {
  border: 0.125rem solid #ffffff;
  margin: 4px;
  min-width: 100px;
  white-space: nowrap;
  font: var(--unnamed-font-style-normal) normal bold var(--unnamed-font-size-14) /
