<template>
  <div ref="content">
    <div class="mobile-screen">
      <div id="my_datavizimp"></div>
    </div>
  </div>
</template>

<script>
import * as d3 from "d3";
import "d3-scale-chromatic";
import i18n from "@/plugins/i18n";
export default {
  name: "ImprovementExpenseComp",
  components: {},
  props: {
    dataset: {
      type: [Array, Object, String],
      default: null,
    },
    colors: {
      type: [Array, Object, String],
      default: null,
    },
    labels: {
      type: [Array, Object, String],
      default: null,
    },
    dropDownList: {
      type: [Array, Object, String],
      default: null,
    },
    optionlist: {
      type: [Array, Object, String],
      default: null,
    },
    expenseType: {
      type: String,
      default: "expense",
    },
    secondGraph: {
      type: Boolean,
      default: false,
    },
    totalMonths: {
      type: [String, Number, Object],
      default: 0,
    },
    startYear: {
      type: [Array, Object, String, Number],
      default: null,
    },
    startMonth: {
      type: [Array, Object, String, Number],
      default: null,
    },
    xLabelsProp: {
      type: [Array, Object, String, Number],
      default: null,
    },
    xMinMaxProp: {
      type: [Array, Object, String, Number],
      default: null,
    },
  },
  data() {
    return {
      circleRadius: 6,
      svg: null,
      getData: [],
      height: null,
      width: null,
      currentOpacity: null,
      selectedOption: "",
      allSelectOptions: [],
      allGroupOptions: [],
      ellipseColor: ["white", "lightgray"],
    };
  },
  computed: {
    getScreen() {
      return window.screen.width;
    },
    getallGroupOptions() {
      return this.allSelectOptions;
    },
    isMobileScreen() {
      if (
        navigator.userAgent.match(/Android/i) ||
        navigator.userAgent.match(/webOS/i) ||
        navigator.userAgent.match(/iPhone/i) ||
        navigator.userAgent.match(/iPad/i) ||
        navigator.userAgent.match(/iPod/i) ||
        navigator.userAgent.match(/BlackBerry/i) ||
        navigator.userAgent.match(/Windows Phone/i)
      ) {
        return true;
      } else {
        return false;
      }
    },
  },

  created() {
    i18n.locale = "ja";
    localStorage.setItem("locale", "ja");
  },

  async mounted() {
    this.getData = this.dataset;
    if (this.getData.length > 0) {
      await this.draw();
    }
  },

  methods: {
    /**Function to draw based on data and display in graph format */
    draw() {
      /** set the dimensions and margins of the graph*/
      var margin = { top: 10, right: 70, bottom: 30, left: 200 };
      (this.width = 950 - margin.left - margin.right),
        (this.height = 470 - margin.top - margin.bottom);

      d3.select("svg #my_datavizimp").remove();
      /** append the svg object to the body of the page */

      // let setScreenWidth = this.getScreen > 500 ? "100%" : "600";

      this.svg = d3
        .select("#my_datavizimp")
        .append("svg")
        .attr("viewBox", "0 0 1000 510")
        .attr("width", "100%")
        .append("g")
        .attr("transform", "translate(" + 0 + "," + margin.top + ")");

      // this.svg
      //   .append("rect")
      //   .attr("x", 0)
      //   .attr("y", 0)
      //   .attr("height", this.height - 69)
      //   .attr("width", "100%")
      //   .style("stroke", "#000")
      //   .style("fill", "none")
      //   .style("stroke-width", 2);

      let xx = this.labels.length;
      let barSize = this.height / (xx + 1);

      let i, mmm;
      mmm = [];
      for (i = 0; i < xx; i++) {
        mmm.push(i * 15);
      }

      var bar = this.svg
        .selectAll("g")
        .data(mmm)
        .enter()
        .append("g")
        .attr("transform", function (d, i) {
          return "translate(" + 80 + "," + i * barSize + ")";
        });

      // bar
      //   .append("rect")
      //   .attr("fill", () => {
      //     return "white";
      //   })
      //   .attr("class", "bar")
      //   .attr("stroke", "#222")
      //   .attr("height", barSize)
      //   .attr("width", "90%")
      //   .style("stroke-dasharray", "2,2");
      let myColorList = ["#3FB0F0", "#A9D28E", "#FEB653", "#FEAFE6", "#F89C9F"];
      bar
        .append("line")
        .attr("x1", 0)
        .attr("y1", 0)
        .attr("x2", 0)
        .attr("y2", barSize)
        .attr("stroke", (d, i) => {
          let x;
          x = !this.isMobileScreen ? myColorList[i] : "white";
          return x;
        })
        .attr("style", "stroke-width:3");
      bar
        .append("line")
        .attr("x1", 0)
        .attr("y1", barSize)
        .attr("x2", "90%")
        .attr("y2", barSize)
        .attr("style", function (d, i) {
          if (i == 4) {
            return "stroke:#000;stroke-width:1;";
          }
          return "";
        });
      bar
        .append("line")
        .attr("x1", "90%")
        .attr("y1", 0)
        .attr("x2", 0)
        .attr("y2", 0)
        .attr("style", "stroke:#222;stroke-width:0.5;stroke-dasharray:3,3");
      // .attr("style", "border-left:1px solid white");
      var tooltip = d3
        .select("body")
        .append("div")
        .attr("class", "tooltip3")
        .style("opacity", 0);
      // let domainName = ["金食い虫", "肥満体", "ぽっちゃり", "標準", "マッチョ"];
      let labelList = this.$t("GRAPH.LEGENDS").map((item, index) => {
        return {
          name: item.LEGEND,
          meaning: item.MEANING,
          color: myColorList[index],
        };
      });
      bar
        .select("foreignObject")
        .data(labelList)
        .enter()
        .append("foreignObject")
        .attr("transform", function (d, i) {
          return "translate(" + 0 + "," + i * barSize + ")";
        })
        .attr("x", 2)
        .attr("y", barSize - 50)
        .attr("width", 60)
        .attr("height", 20)
        .attr("stroke", "black")
        .attr("text-anchor", "middle")
        .append("xhtml:body")
        .html((d) => {
          if (this.secondGraph) {
            let x = d.split(" ");
            return `<div class='text-center'>${
              x[0]
            }</div><div class='text-center'>${x[1] ? x[1] : ""}</div>`;
          }
          return `<div style="color:${d.color}">${d.name}</div>`;
        })
        .on("mouseover", (d) => {
          if (!this.isMobileScreen) {
            tooltip.transition().duration(200).style("opacity", 0.9);
            tooltip
              .html(
                `<div class="textcolorLable">` +
                  d.meaning +
                  `</div>` +
                  `<div class="arrow-down-lable ml-6 "></div>`
              )
              .style("left", d3.event.pageX - 80 + "px")
              .style("top", d3.event.pageY - 55 + "px");
          }
        })
        .on("mouseout", function () {
          if (!this.isMobileScreen) {
            tooltip.transition().duration(500).style("opacity", 0);
          }
        })
        .attr(
          "style",
          "text-align:left;word-break:break-word;background:transparent;font-size:14px;font-weight:700"
        );

      let xLabels = [];
      let xLablesMinMax = [];
      let startMonth = this.startMonth,
        startYear = this.startYear;
      for (let index = 0; index < this.totalMonths; index++) {
        let x = startYear + this.$t("YEAR") + startMonth + this.$t("MONTH");
        xLabels.push(x);
        let xMinMax =
          String(startYear) +
          (startMonth > 9 ? String(startMonth) : `0${startMonth}`);

        xLablesMinMax.push(parseInt(xMinMax));
        if (startMonth >= 12) {
          startYear = startYear + 1;
          startMonth = 1;
        } else {
          if (startMonth >= 1 && startMonth < 12) {
            startMonth = startMonth + 1;
          }
        }
      }

      let newLabelMinMax = [];
      //   this.getData = this.getData.map((data) => {
      //     return this._.sortBy(data.performance, "judg_monthly");
      //   });
      //   this.getData = this._.sortBy(this.getData, "judg_monthly");
      console.log("getData", this.getData);
      var dataReady = this.getData.map((grpName) => {
        return {
          name: grpName.expenseItem,
          values: this._.sortBy(
            grpName.performance.map(function (d) {
              return {
                time: xLablesMinMax.indexOf(d.judg_monthly),
                time1: d.judg_monthly,
                value: d.judg_level - 0.4,
                gname: d.expense_item_nm,
              };
            }),
            "time"
          ),
        };
      });

      if (
        xLabels.length == xLablesMinMax.length &&
        xLablesMinMax.length == this.totalMonths
      ) {
        this.update(
          dataReady,
          xLablesMinMax,
          newLabelMinMax,
          xLabels,

          this.svg,
          this.height,
          this.width
        );
      }
    },
    update(dataReady, xLablesMinMax) {
      /** Add X axis --> it is a date format */

      // let gnameListCount = dataReady.map((item) => item.name);
      // let myColorList = [
      //   "red",
      //   "blue",
      //   "green",
      //   "yellow",
      //   "orange",
      //   "tomato",
      //   "pink",
      // ];
      let myColorList = [
        "#3366cc",
        "#dc3912",
        "#ff9900",
        "#109618",
        "#990099",
        "#0099c6",
        "#dd4477",
        "#66aa00",
        "#b82e2e",
        "#316395",
        "#994499",
        "#22aa99",
        "#aaaa11",
        "#6633cc",
        "#e67300",
        "#8b0707",
        "#651067",
        "#329262",
        "#5574a6",
        "#3b3eac",
      ];
      let gnameList = dataReady.map((item, index) => {
        return { name: item.name, color: myColorList[index] };
      });
      // alert(gnameList);

      var x = d3
        .scaleLinear()
        .domain([0, xLablesMinMax.length - 1])
        .range([60, this.width + 120]);

      var xAxis = d3
        .axisBottom(x)
        .ticks(this.totalMonths - 1)
        .tickSizeInner(5)
        .tickSizeOuter(5)
        .tickPadding(15)
        // .tickValues(xLablesMinMax)
        .tickFormat((d, i) => {
          let year = String(xLablesMinMax[i]).slice(0, 4);
          let month = String(xLablesMinMax[i]).slice(4, 6);
          return `${year}年${month}月`;
        });
      var ticksList = this.svg
        .append("g")
        .attr("transform", "translate(100," + (this.height - 70) + ")")
        .call(xAxis)
        .call((g) => g.selectAll(".domain").attr("style", "display:none"))
        // .call((g) => g.selectAll(".tick line").attr("style", "display:none"))
        .call(
          (g) =>
            g
              .selectAll(".tick text")
              .attr("dy", () => {
                return this.isMobileScreen ? 0 : 14;
              })
              .attr("dx", () => (this.isMobileScreen ? -40 : 10))
              .attr("transform", () => {
                return this.isMobileScreen ? "rotate(-55)" : "rotate(0)";
              })
              // .attr("transform", () => {
              //   return this.isMobileScreen ? "rotate(-45)" : "rotate(0)";
              // })
              .attr("style", "font-size:10px;font-weight:700")
          // .attr("dx", () => (this.isMobileScreen ? -25 : 10))
        )
        .call((g) =>
          g.selectAll(".tick").attr("style", "font-weight:700;color:#033170")
        );

      var ticks = ticksList.selectAll(".tick");
      ticks.each(function () {
        d3.select(this)
          .append("circle")
          .attr("r", 4)
          .attr("fill", "gray")
          .attr("stroke", "gray");
      });
      // ticks.selectAll("line").remove();
      var div = d3
        .select("body")
        .append("div")
        .attr("class", "tooltip3")
        .style("opacity", 0);
      /** Add Y axis */
      var y = d3
        .scaleLinear()
        .domain([0, 25])
        .range([this.height - 73, 0]);

      var yAxis = d3.axisLeft(y);

      this.svg
        .append("g")
        .attr("stroke-width", "2")
        .call(yAxis)
        .call((g) => g.selectAll(".domain").attr("style", "display:none"));
      // .attr("transform", "translate(0," + this.height + ")")
      // .ticks(this.totalMonths - 1)
      // .tickValues(xLablesMinMax)
      this.svg.append("g").style("stroke", () => {
        // alert(d3.max(y.domain()));
      });
      /** Add the lines */
      var line = d3
        .line()
        .x(function (d) {
          return x(+d.time) + 100;
        })
        .y(function (d) {
          return y(+d.value);
        });

      this.svg
        .selectAll("myLines")
        .data(dataReady)
        .enter()
        .append("path")
        .attr("class", function (d) {
          return d.name;
        })
        .attr("d", function (d) {
          return line(d.values);
        })
        .attr("stroke", () => {
          return "#123251";
        })
        .style("stroke-width", 1)
        .style("fill", "none");

      // this.svg.exit().remove();
      /** Add the points */
      var nodes = this.svg
        /** First we need to enter in a group */
        .selectAll("myDots")
        .data(dataReady)
        .enter()
        .append("g")
        .style("fill", (d, i) => {
          if (this.secondGraph == true) {
            return this.ellipseColor[i];
          } else {
            return "white";
          }
        });
      /** Second we need to enter in the 'values' part of this group */
      // let myColor = d3.scaleOrdinal().domain(dataReady).range(d3.schemeSet3);
      let domainName = this.$t("GRAPH.LEGENDS").map((item) => item.LEGEND);
      nodes
        .selectAll("circle")
        .data((d) => {
          return d.values;
        })
        .enter()
        .append("circle")
        .attr("class", "dot")
        .attr("cx", (d) => {
          return x(d.time) + 100;
        })
        .attr("cy", (d) => {
          return y(d.value);
        })
        .attr("r", this.circleRadius)
        .on("mouseover", function (d) {
          // transition the mouseover'd element
          // to having a red fill
          // console.log("i",i,d)
          let yName;
          if (d.value <= 5) {
            yName = domainName[4];
          } else if (d.value <= 10) {
            yName = domainName[3];
          } else if (d.value <= 15) {
            yName = domainName[2];
          } else if (d.value <= 20) {
            yName = domainName[1];
          } else {
            yName = domainName[0];
          }
          div.transition().duration(200).style("opacity", 0.9);
          div
            .html(
              `<div class="textcolor2">` +
                d.gname +
                `</div>` +
                `<div class="textcolor">` +
                yName +
                `</div>` +
                `<div class="arrow-down ml-4 "></div>`
            )
            .style("left", d3.event.pageX - 30 + "px")
            .style("top", d3.event.pageY - 70 + "px")
            .style("font-weight", "900");
          d3.select(this).transition().attr("r", 14);
          // .attr('fill', '#ff0000');
        })
        // .attr("ry", 25)
        .attr("fill", (d) => {
          let getObj = this._.find(gnameList, { name: d.gname });
          if (d.gname == getObj.name) {
            return getObj.color;
          }
        })

        .on("mouseout", function () {
          div.transition().duration(500).style("opacity", 0);
          d3.select(this).transition().attr("r", 6);
        });

      // nodes.selectAll("circle").on("hover", () => {
      //   this.$router.push({ name: "expense_evaluation" });
      // });
      // .attr("stroke", "gray");

      //   nodes
      //     .selectAll("foreignObject")
      //     .data(function (d) {
      //       return d.values;
      //     })
      //     .enter()
      //     .append("foreignObject")
      //     .attr("x", function (d) {
      //       return x(d.time) + 60;
      //     })
      //     .attr("y", function (d) {
      //       return y(d.value) - 10;
      //     })
      //     .attr("width", 80)
      //     .attr("height", 90)
      //     .attr("stroke", "black")
      //     .attr("text-anchor", "middle")
      //     .attr("style", "color:white")
      //     .append("xhtml:body")
      //     .html(function (d) {
      //       return d.gname;
      //     })
      //     .attr(
      //       "style",
      //       "text-align:center;font-size:13px;background:transparent;"
      //     );
    },
    yealyExpense() {
      this.$router.push({ name: "improvement_expense" });
    },
    monthlyExpense() {
      this.$router.push({ name: "improvement_expense_judgement" });
    },
    onExpenseClick() {
      this.$emit("expense-change", "");
    },
  },
};
</script>

<style  lang="scss">
.btn-custom-color {
  background-color: #062854;
  border-color: #062854;
  color: white;
}
.mobile-screen {
  overflow-x: auto;
}

div.tooltip3 {
  position: absolute;
  text-align: center;
  // right: 200;
  width: 60px;
  height: 28px;
  padding: 2px;
  font: 12px sans-serif;
  // background: lightsteelblue;
  border: 0px;
  border-radius: 8px;
  pointer-events: none;
}

.textcolor {
  color: black;
  background-color: white;
  width: 120%;
  height: 100%;
  border: 1x;
  border-radius: 0px;
  border-style: solid;
  border-color: whitesmoke;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.textcolor2 {
  padding-top: 1px;

  color: white;
  background-color: green;
  width: 120%;
  height: 100%;
  border: 0px;
  border-radius: 0px;
  border-style: solid;
  border-color: green;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.arrow-down {
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid #ffffff;
}

.textcolorLable {
  width: 144px;
  height: auto;
  background: #eff5fd 0% 0% no-repeat padding-box;
  box-shadow: 0px 0px 30px #0000001a;
  border: 1px solid #033170;
  opacity: 1;
  color: #1e1e24;
}

.arrow-down-lable {
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid #033170;
}

.ml-6 {
  margin-left: 4rem !important;
}
</style>